import React, { Component } from 'react';

class ChartsDemo extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <iframe src="http://gor181.github.io/react-chartjs-2/" width="100%">
      </iframe>
    );
  }
}

export default ChartsDemo;


